.App {
    text-align: center;
}

body {
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background: linear-gradient(135deg, #8bdf76, #9b59b6, #d12b41);
}

.container {
    width: 700px;
    background: white;
    padding: 20px 25px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.container .title {
    font-size: 30px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 36px;
    font-style: bold;
    position: relative;
}

.container .title::before {
    content: '';
    height: 3px;
    width: 30px;
    background: linear-gradient(135deg, #8bdf76, #9b59b6, #d12b41);
    position: absolute;
    left: 0;
    bottom: 0;
    border-radius: 1px;
    -webkit-border-radius: 1px;
    -moz-border-radius: 1px;
    -ms-border-radius: 1px;
    -o-border-radius: 1px;
}

.container form .user-details {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.container form .user-details .input-box {
    width: calc(100% /2 - 15px);
    margin-bottom: 5px;
}

.container form .user-details .input-box .details {
    font-size: 18px;
}

.container form .user-details .input-box input {
    height: 45px;
    width: 100%;
    font-size: 15px;
    outline: none;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    border: 1px solid #ccc;
    padding-left: 5px;
}

.container form .user-details .input-box input:focus,
.container form .user-details .input-box input:valid {
    border-color: aqua;
}

.form-select {
    margin-top: 15px;
    margin-bottom: 7px;
}

.btn-outline-primary {
    width: 100%;
}

@media (max-width:584px) {
    .container {
        max-width: 100%;
    }
    .container form .user-details .input-box {
        width: 100vh;
        margin-bottom: 5px;
    }
    .container {
        display: flex;
        width: auto;
        background: white;
        padding: 20px 25px;
        border-radius: 5px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
    }
}